/**
 * Utility functions for handling Google Drive URLs
 */

/**
 * Converts various Google Drive URL formats to direct image display URLs
 * @param url - The original Google Drive URL
 * @returns Formatted URL for direct image display
 */
export function formatGoogleDriveImageUrl(url: string): string {
    if (!url || !url.includes('drive.google.com')) {
        return url;
    }

    // If already in correct format, return as is
    if (url.includes('uc?id=') && url.includes('export=view')) {
        return url;
    }

    // Extract file ID from various Google Drive URL formats
    let fileId = '';

    // Format: https://drive.google.com/file/d/FILE_ID/view
    const fileIdMatch1 = url.match(/\/d\/([a-zA-Z0-9-_]+)/);
    if (fileIdMatch1) {
        fileId = fileIdMatch1[1];
    }

    // Format: https://drive.google.com/open?id=FILE_ID
    const fileIdMatch2 = url.match(/[\?&]id=([a-zA-Z0-9-_]+)/);
    if (fileIdMatch2) {
        fileId = fileIdMatch2[1];
    }

    // Format: https://drive.google.com/uc?id=FILE_ID (add export parameter)
    const fileIdMatch3 = url.match(/uc\?id=([a-zA-Z0-9-_]+)/);
    if (fileIdMatch3) {
        fileId = fileIdMatch3[1];
    }

    if (fileId) {
        return `https://drive.google.com/uc?id=${fileId}&export=view`;
    }

    // If no file ID found, return original URL
    return url;
}

/**
 * Validates if a URL is a valid image URL (including Google Drive)
 * @param url - The URL to validate
 * @returns True if the URL is valid for image display
 */
export function isValidImageUrl(url: string): boolean {
    if (!url) return false;
    
    return (
        url.startsWith('/') || 
        url.startsWith('http://') || 
        url.startsWith('https://') ||
        url.includes('drive.google.com')
    );
}

/**
 * Processes an image URL for optimal display (formats Google Drive URLs and validates)
 * @param url - The image URL to process
 * @param fallbackUrl - Fallback URL if the original is invalid
 * @returns Processed URL ready for display
 */
export function processImageUrl(url: string, fallbackUrl: string): string {
    if (!isValidImageUrl(url)) {
        return fallbackUrl;
    }
    
    return formatGoogleDriveImageUrl(url);
}

/**
 * Checks if a URL is a Google Drive URL
 * @param url - The URL to check
 * @returns True if the URL is from Google Drive
 */
export function isGoogleDriveUrl(url: string): boolean {
    return Boolean(url && url.includes('drive.google.com'));
} 